.home {
	display: flex;
	align-items: flex-end;
	justify-content: center;

	width: 100%;
	height: 100vh;
	background: rgb(133, 147, 151);
	background: linear-gradient(45deg,
			rgb(64, 206, 83) 0%,
			rgb(23, 34, 80) 20%,
			rgb(216, 136, 32) 100%);
}

.cont {
	width: 45vw;
	height: 80vh;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	gap: 1vw;
}

.hello {
	text-align: start;
	color: white;
	font-size: 3.8vw;
	font-weight: 700;
}

.typinganimation {
	font-size: 4vw;
	color: #7565ee;
	font-weight: 700;
}

.aboutme {
	text-align: start;
	color: white;
	width: 30vw;
	font-size: 1.2vw;
}

.buttons {
	width: 40vw;
	height: auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	gap: 3vw;
	align-items: center;
}

.hirebutton {
	font-family: "Roboto", sans-serif;
	font-family: "Josefin Sans", sans-serif;
	width: 10vw;
	height: 5vh;
	font-size: 1vw;
	font-weight: bold;
	color: white;
	background-color: black;
	border: none;
	border-radius: 10px;
	transition: 0.5s;
}

.hirebutton:hover {
	transition-timing-function: ease-in-out;
	transition: 0.5s;
	transform: scale(1.03);
}

.hirebutton:active {
	transition: none;
	transform: translateY(2px);
}

.resumebutton {
	display: flex;
	flex-direction: row;
	font-family: "Roboto", sans-serif;
	font-family: "Josefin Sans", sans-serif;
	width: 10vw;
	height: 5vh;
	font-size: 1vw;
	font-weight: bold;
	color: black;
	background-color: white;
	border: none;
	border-radius: 10px;
	align-items: center;
	justify-content: center;
	gap: 0.3vw;
	transition: 0.5s;
}

.resumebutton:hover {
	transition-timing-function: ease-in-out;
	transition: 0.5s;
	transform: scale(1.03);
}

.resumebutton:active {
	transition: none;
	transform: translateY(2px);
}

.smicons {
	width: 20vw;
	height: 10vh;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	gap: 2.5vw;
	align-items: center;
}

.icon1 {
	color: black;
}

.icon2 {
	color: black;
}

.icon3 {
	color: black;
}

.icon4 {
	color: black;
}

.icon1:hover {
	color: white;
}

.icon2:hover {
	color: white;
}

.icon3:hover {
	color: white;
}

.icon4:hover {
	color: white;
}

.cont1 {
	width: 40vw;
	height: 80vh;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-end;
}

.mephoto {
	width: 35vw;
	height: auto;
	border-radius: 100% 50% 50% 60%;
	background-color: #7209b7;
	z-index: 2;
	box-shadow: 0px 0px 182px -5px rgba(0, 0, 0, 1);
	-webkit-box-shadow: 0px 0px 182px -5px rgba(0, 0, 0, 1);
	-moz-box-shadow: 0px 0px 182px -5px rgba(0, 0, 0, 1);
}

.circle1 {
	position: absolute;
	left: 55%;
	top: 20%;
	width: 5vw;
	height: auto;
	background-color: #2a2f5a;
	border-radius: 50% 50%;
	z-index: 1;
	aspect-ratio: 1;
}

.circle2 {
	position: absolute;
	left: 80%;
	top: 20%;
	width: 15vw;
	height: auto;
	background-color: #685ebe;
	border-radius: 50% 50%;
	aspect-ratio: 1;
	z-index: 1;
}

@media screen and (max-width: 480px) {
	.home {
		width: 100%;
		height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.cont {
		width: 100%;
		height: 70%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: center;
		gap: 3%;
	}

	.cont1 {
		width: 100%;
		height: 30%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}

	.smicons {
		width: 100%;
		height: 5vh;
		display: flex;
		flex-direction: row;
		justify-content: space-evenly;
		align-items: center;
		scale: 80%;
	}

	.buttons {
		width: 100%;
		height: auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
		gap: 3vw;
		align-items: center;
	}

	.hirebutton {
		font-family: "Roboto", sans-serif;
		font-family: "Josefin Sans", sans-serif;
		width: 40vw;
		height: 4.2vh;
		font-size: 4vw;
		font-weight: bold;
		color: white;
		background-color: black;
		border: none;
		border-radius: 10px;
		transition: 0.5s;
	}

	.resumebutton {
		display: flex;
		flex-direction: row;
		font-family: "Roboto", sans-serif;
		font-family: "Josefin Sans", sans-serif;
		width: 39vw;
		height: 4vh;
		font-size: 4vw;
		font-weight: bold;
		color: black;
		background-color: white;
		border: none;
		border-radius: 10px;
		align-items: center;
		justify-content: center;
		gap: 0.3vh;
		transition: 0.5s;
	}

	.hello {
		width: 80%;
		text-align: start;
		color: white;
		font-size: 10vw;
		font-weight: 700;
	}

	.typinganimation {
		width: 100%;
		font-size: 6vw;
		color: #ff6dae;
		font-weight: 700;
	}

	.aboutme {
		text-align: start;
		color: white;
		width: 80%;
		font-size: 4.8vw;
	}

	.circle1 {
		display: none;
	}

	.circle2 {
		display: none;
	}

	.mephoto {
		width: auto;
		height: 100%;
		scale: 70%;
		border-radius: 100% 50% 50% 60%;
		background-color: #7209b7;
		z-index: 2;
		box-shadow: 0px 0px 182px -5px rgba(0, 0, 0, 1);
		-webkit-box-shadow: 0px 0px 182px -5px rgba(0, 0, 0, 1);
		-moz-box-shadow: 0px 0px 182px -5px rgba(0, 0, 0, 1);
	}
}