.navbar {
	position: fixed;
	display: flex;
	flex-direction: row;
	width: 100%;
	height: 10%;
	z-index: 5;
}

.navbarname {
	width: 25%;
	height: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 3vw;
	font-weight: bold;
	color: rgb(0, 0, 0);
}

.navbarmenu {
	width: 70%;
	height: auto;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	font-size: 1.3vw;
	font-weight: 450;
	gap: 3vw;
}

.hiremebutton {
	font-family: "Roboto", sans-serif;
	font-family: "Josefin Sans", sans-serif;
	width: 14%;
	height: 50%;
	text-align: center;
	font-size: 70%;
	font-weight: bold;
	color: white;
	background-color: rgb(22, 48, 61);
	border: none;
	border-radius: 10px;
	transition: 0.5s;
	font-size: 1.5vw;
}

.hiremebutton:hover {
	transition-timing-function: ease-in-out;
	transition: 0.5s;
	transform: scale(1.03);
}

.hiremebutton:active {
	transition: none;
	transform: translateY(2px);
}

@media screen and (max-width: 480px) {
	.navbar {
		position: fixed;
		display: flex;
		flex-direction: row;
		width: 100%;
		height: 8%;
		z-index: 5;
	}

	.navbarname {
		display: none;
	}

	.navbarmenu {
		width: 100%;
		height: auto;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		font-size: 3.5vw;
		font-weight: 450;
		gap: 4vw;
	}

	.hiremebutton {
		font-family: "Roboto", sans-serif;
		font-family: "Josefin Sans", sans-serif;
		width: 20%;
		height: 38%;
		text-align: center;
		font-size: 70%;
		font-weight: bold;
		color: white;
		background-color: rgb(255, 0, 0);
		border: none;
		border-radius: 10px;
		transition: 0.5s;
	}
}